.full-width-list {
    ul {
        padding: 0;
        margin: 0;
        list-style-type: none;
    }
    li a {
        border-top: 1px solid $brand-tint-2;
        display: block;
        font-weight: 700;
        color: $brand-dark-blue;
        padding: 20px 0 20px 0;
        text-decoration: none;
        border-radius: 4px;
    }
    li a:hover {
        background-color: $brand-blue;
        color: #fff;
        cursor: pointer;
    }
}

.full-width-list-border {
    ul {
        margin: 20px 0;
    }
    li {
        margin: 10px 0;
    }
    li a {
        border: 1px solid $brand-tint-2;
    }
}