@media(min-width:768px) {
    .vcenter-container {
        position: relative;
        width: 100%;
        height: 100vh;
    }
    .vcenter-container-one-page {
        position: relative;
        width: 100%;
        height: 85vh;
    }
    .vcenter {
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        -ms-transform: translateY(-50%);
        -webkit-transform: translateY(-50%);
    }
    .vcenter-40 {
        margin-top: -80px !important;
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        -ms-transform: translateY(-50%);
        -webkit-transform: translateY(-50%);
    }
}

@media(max-height:800px) {
    .vcenter {
        margin-top: 150px;
        top: 0%;
        transform: none;
        -ms-transform: none;
        -webkit-transform: none;
    }
}

@media(max-device-height:800px) {
    .vcenter {
        margin-top: 0;
    }
}