.table-striped>tbody>tr:nth-of-type(odd) {
    background-color: $brand-tint-1;
}

.theme-default {
    all: unset;
}

.modal-table {
    padding-bottom: 10px !important;
}

.table {
    padding-bottom: 30px;
    tbody {
        th {
            border-top: none !important;
            padding: 20px 4px;
        }
        td {
            border-top: none !important;
            font-weight: 700;
        }
        td:nth-child(2) {
            color: $brand-blue;
        }
    }
}